import PressCell from 'press-ui/press-cell/press-cell.vue';
import { showDialog, showConfirmDialog } from 'press-ui/press-dialog-plus';
import PressDialogPlus from 'press-ui/press-dialog-plus/press-dialog-plus.vue';


const gBeforeClose = action => new Promise((resolve) => {
  if (action === 'confirm') {
    setTimeout(() => {
      resolve(true);
    }, 1000);
  } else {
    // false可以拦截取消操作
    resolve(true);
  }
});


export default {
  i18n: {
    'zh-CN': {
      alert1: '提示弹窗',
      alert2: '提示弹窗（无标题）',
      confirm: '确认弹窗',
      asyncClose: '异步关闭',
      roundButton: '圆角按钮样式',
      componentCall: '组件调用',
      content: '代码是写出来给人看的，附带能在机器上运行',
    },
    'en-US': {
      alert1: 'Alert',
      alert2: 'Alert without title',
      confirm: 'Confirm dialog',
      asyncClose: 'Async Close',
      roundButton: 'Round Button Style',
      componentCall: 'Component Call',
    },
  },

  components: {
    PressDialogPlus,
    PressCell,
  },
  data() {
    return {
      show: false,
    };
  },
  methods: {
    onShowDialog(type) {
      if (type === 'component') {
        this.show = true;
        return;
      }

      let title = this.t('title');
      // let showCancelButton = false;
      let beforeClose = null;
      let func = showDialog;

      if (type === 'noTitle') {
        title = '';
      } else if (type === 'cancel') {
        // showCancelButton = true;
        func = showConfirmDialog;
      } else if (type === 'async') {
        // showCancelButton = true;
        beforeClose = gBeforeClose;
        func = showConfirmDialog;
      }


      func({
        context: this,
        title,
        // showCancelButton,
        message: this.t('content'),
        beforeClose,
      })
        .then(() => {
          console.log('dialog resolve');
        })
        .catch(() => {
          console.log('dialog reject');
        });
    },
    getUserInfo(res) {
      console.log('getUserInfo.res', res);
    },
    onClose() {
      this.show = false;
    },
  },
};
